.input-textarea {
  border-bottom-width: 0;
  border-color: #ddd;
  border-left: none;
  border-right: none;
  border-top: none;
  color: rgba(0, 0, 0, 0.87);
  width: 100%;
  padding: 20px;
  min-height: 280px;
  font-size: 18px;
  line-height: 28px;
  resize: none;

  overflow: auto hidden;
  box-sizing: border-box;
  direction: ltr;
}

.input-textarea:focus {
  border-bottom: 0;
  outline: none !important;
}

.result-text {
  display: inline;
  padding: 0;
  font-size: 18px;
  line-height: 28px;
  color: rgba(0, 0, 0, 0.87);
  word-wrap: break-word;
}
